.bg-background:before {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	right: 0;
	display: block;
	z-index: 1;
	top: 0;
}

.background-text {
	position: relative;
	z-index: 10;
}

.bg-background2:before,
.bg-background-color:before {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	right: 0;
	display: block;
	z-index: 1;
	top: 0;
}

.bg-background3 {
	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		right: 0;
		display: block;
		z-index: 1;
		top: 0;
	}
	.header-text {
		position: relative;
		z-index: 10;
	}
}

.bg-background-color .content-text,
.bg-background .header-text1,
.bg-background2 .header-text {
	position: relative;
	z-index: 10;
}

