	background-size: cover;
	position: relative;
	.header-text {
		position: relative;
		z-index: 10;
	}
}

.background-text1 {
	color: #FFF;
	padding: 2px 8px;
	position: absolute;
	top: 30%;
	right: 0;
	left: auto;
	width: 100%;
	bottom: 0;
	height: auto;
	z-index: 9;
	font-size: 18px;
	h1 {
